import React from "react"

const OptionsColor = ({ value, label, selected, dark, onClick }) => (
  <div
    onClick={onClick}
    style={{
      paddingRight: "5px",
      border: 0,
      borderRadius: 0,
      boxShadow: "none",
    }}
  >
    <div
      style={{
        height: "70px",
        border: selected ? "solid 4px red" : 0,
        backgroundColor: value,
      }}
    />
    <span style={{ color: dark ? "black" : "white" }}>{label}</span>
  </div>
)
export default OptionsColor
