import React from "react"
import { Grid } from "semantic-ui-react"
import { useMediaQuery } from "react-responsive"
import chunk from "lodash/chunk"
import breakpoints from "../../../breakpoints"
import OptionsColor from "./OptionsColor"

const ResponsiveColors = props => {
  const isDesktop = useMediaQuery({
    query: `(min-width: ${breakpoints.desktop.min}px)`,
  })
  return isDesktop ? (
    <Colors numberOfChunks={3} {...props} />
  ) : (
    <Colors numberOfChunks={2} {...props} />
  )
}

const Colors = ({ numberOfChunks, color, colors, dark, onSelect }) => (
  <React.Fragment>
    {chunk(colors, numberOfChunks).map((colorsChunk, i) => (
      <Grid.Row
        key={"row" + i}
        columns="equal"
        style={{ paddingTop: "6px", paddingLeft: "13px", paddingRight: "8px" }}
      >
        {colorsChunk.map(({ id, value, label }, j) => (
          <Grid.Column key={"column" + i + j} style={{ padding: 0 }}>
            <OptionsColor
              value={value}
              label={label}
              selected={color === id}
              dark={dark}
              onClick={() => onSelect(id)}
            />
          </Grid.Column>
        ))}
      </Grid.Row>
    ))}
  </React.Fragment>
)

export default ResponsiveColors
